.c-genre-list__container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: -20px;
  overflow-y: auto;
  padding-top: 81px;
}
.c-genre-list {
  text-align: right;
  padding: 36px 20px 58px 0;
}
.c-genre-list .c-genre .c-genre__name {
  cursor: pointer;
  font-size: 24px;
  color: #545454;
  padding: 12px 0 12px 12px;
  display: inline-block;
}
.c-genre-list .c-genre .c-genre__name:hover {
  color: #000;
}
.c-group-list {
  text-align: right;
  margin: -5px 0 0;
}
.c-group-list .c-group {
  cursor: pointer;
  display: inline-block;
  font-weight: 300;
  font-family: 'Helvetica Neue', 'Helvetica Neue Light Woff', 'Roboto', 'sans-serif', 'Arial';
  margin: 4px 0 8px 35px;
  font-size: 20px;
  color: #afafaf;
}
.c-group-list .c-group:hover {
  color: #000;
}
.translate-from-right-transition .c-group {
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  transition-timeout: 0.2s;
  opacity: 1;
  -ms-filter: none;
  -webkit-filter: none;
          filter: none;
}
.translate-from-right-enter .c-group {
  -webkit-transform: translateX(-40px);
  transform: translateX(-40px);
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity=0);
}


