.c-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: box;
  display: flex;
  -webkit-box-orient: vertical;
  -o-box-orient: vertical;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -o-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -o-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  background: rgba(255,255,255,0.85);
  z-index: 50;
}
.c-modal--interact p {
  padding: 5px 20px;
  text-align: center;
}
#app {
  position: relative;
  height: 100%;
  max-width: 600px;
  margin: 0 auto;
}
#app .o-btn-back {
  position: absolute;
  left: 0;
  bottom: 20px;
}
#app .o-btn-back.opacity-leave {
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
  -webkit-transform: translateY(-10px) scale(2) !important;
  transform: translateY(-10px) scale(2) !important;
}
#app .o-btn-back:hover {
  -webkit-transform: translateY(-10px);
  transform: translateY(-10px);
}
.c-header {
  font-size: 36px;
  font-weight: 100;
  font-family: 'Helvetica Neue', 'Helvetica Neue Thin Woff', 'Roboto', 'sans-serif', 'Arial';
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-top: 20px;
  background: rgba(255,255,255,0.7);
  position: relative;
  z-index: 1;
}
@media (max-width: 360px), (orientation: landscape) and (max-height: 370px) {
  .c-header {
    font-size: 28px;
  }
}
.c-header hr {
  margin-top: 15px;
  border: 1px solid #ededed;
  -webkit-transition: border-color 1s ease;
  transition: border-color 1s ease;
}
.c-header__search {
  margin-left: 3px;
  font-weight: 100;
  font-family: 'Helvetica Neue', 'Helvetica Neue Thin Woff', 'Roboto', 'sans-serif', 'Arial';
  font-size: 36px;
  background: none;
  color: #afafaf;
}
@media (max-width: 360px) {
  .c-header__search {
    font-size: 28px;
  }
}
.c-header__search:focus ~ hr {
  border-bottom: 1px solid #00a2ea;
}
.c-header__group {
  margin-left: 3px;
  font-weight: 300;
  font-family: 'Helvetica Neue', 'Helvetica Neue Light Woff', 'Roboto', 'sans-serif', 'Arial';
  display: inline-block;
}
.o-btn-back {
  width: 50px;
  height: 50px;
  border-radius: 50px;
  border: 1px solid #545454;
  -webkit-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
  -webkit-transition-delay: 0.3s;
  transition-delay: 0.3s;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}
.o-btn-back:after {
  width: 15px;
  height: 15px;
  content: '';
  position: absolute;
  top: 18px;
  left: 16px;
  border: 1px solid #545454;
  border-width: 0 0 1px 1px;
  -webkit-transform: rotate(135deg);
  transform: rotate(135deg);
  -webkit-animation: blink 2s infinite ease;
  animation: blink 2s infinite ease;
}


