.c-splash {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: box;
  display: flex;
  -webkit-box-orient: vertical;
  -o-box-orient: vertical;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -o-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: distribute;
  -o-box-pack: distribute;
  -ms-flex-pack: distribute;
  justify-content: space-around;
}
.c-splash__vk {
  width: 63px;
  height: 36px;
  position: relative;
  bottom: -5px;
}
.c-splash__vk-auth {
  line-height: 36px;
  font-size: 36px;
  opacity: 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity=0);
  -webkit-transition: opacity 0.5s ease;
  transition: opacity 0.5s ease;
}
@media (max-width: 415px) {
  .c-splash__vk-auth {
    font-size: 26px;
  }
}
.c-splash__vk-auth span {
  margin-left: 20px;
}
.c-splash__emusic {
  cursor: pointer;
  pointer-events: auto;
  width: 200px;
  height: 274px;
}
.c-splash__emusic g {
  stroke: #000;
  stroke-width: 1px;
}
.c-splash__emusic path {
  fill: rgba(0,0,0,0);
}
.c-splash__emusic.o-dashoffset-transition path {
  -webkit-animation: blink-color 0.5s 1 forwards 1.5s;
  animation: blink-color 0.5s 1 forwards 1.5s;
  -webkit-transition: all 2s ease;
  transition: all 2s ease;
}
@-webkit-keyframes blink-color {
  0% {
    fill: rgba(0,0,0,0);
  }
  100% {
    fill: #000;
  }
}
@keyframes blink-color {
  0% {
    fill: rgba(0,0,0,0);
  }
  100% {
    fill: #000;
  }
}


