.c-tutorial {
  padding: 40px 0;
  font-family: -apple-system, BlinkMacSystemFont, Roboto, Open Sans, Helvetica Neue, sans-serif;
  font-size: 16px;
  line-height: 23px;
  max-width: 600px;
  margin: 0 auto;
}
.c-tutorial__back {
  cursor: pointer;
}
.c-tutorial__back span {
  margin-left: 20px;
  line-height: 50px;
  vertical-align: top;
}
.c-tutorial .o-btn-back {
  -webkit-transform: rotate(-90deg);
  transform: rotate(-90deg);
}
.c-tutorial p {
  margin: 0 0 20px;
}
.c-tutorial__screenshots {
  margin: 20px -20px 20px 0;
}
.c-tutorial__screenshots img {
  margin-bottom: 20px;
  margin-right: 20px;
  width: 45%;
  max-width: 280px;
  min-width: 240px;
  box-shadow: 0 0 2px #808080;
}


