.c-playlist {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 30;
  overflow: hidden;
}
.c-playlist .o-btn-escape {
  margin-top: 10px;
  float: right;
}
.c-playlist__btns {
  position: relative;
  z-index: 1;
  padding-top: 20px;
  background: rgba(255,255,255,0.7);
}
.c-audio-list {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: -20px;
  padding: 65px 20px 0 0;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
.c-audio {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 46px;
  cursor: pointer;
  font-size: 20px;
  color: #545454;
}
.c-audio__artist {
  font-size: 24px;
  margin-right: 10px;
}
.c-audio__title {
  color: #afafaf;
}
.c-audio__group {
  color: #000;
  float: right;
  clear: both;
  font-weight: 100;
  font-family: 'Helvetica Neue', 'Helvetica Neue Thin Woff', 'Roboto', 'sans-serif', 'Arial';
  margin-left: 10px;
}
@media (max-width: 415px) {
  .c-audio {
    font-size: 16px;
  }
  .c-audio__artist {
    font-size: 20px;
  }
}
.c-playlist__header {
  font-size: 34px;
  display: inline-block;
}


